@import "~@/styles/base.scss";
@import './themes/theme.scss';

:root {
  --text: #050505;
  --background: #fbfbfb;

  --primary: #0050d2;
  --secondary: #00ea07;
  --accent: #e60054;

  --primary-light: #d3fad1;
  --secondary-light: #efdfaf;
  --accent-light: #2bbbb3;

  --primary-dark: #d3fad1;
  --secondary-dark: #efdfaf;
  --accent-dark: #2bbbb3;

  --static: rgba(255, 255, 255, 0.5);
  --static2: rgba(255, 255, 255, 0.2);
}
html {
  scroll-behavior: smooth;
}
body {
  font-family: Helvetica, '微软雅黑', Inter, system-ui, Avenir, Arial,
    sans-serif;
  // 字体渲染时优先考虑易读性，启用字距和连写
  text-rendering: optimizeLegibility;
  // safari 优化
  -webkit-font-smoothing: antialiased;
  // firefox 优化
  -moz-osx-font-smoothing: grayscale;

  scroll-behavior: smooth;
  color: var(--text);
  background-color: var(--background);
  transition: background-color 0.3s, color 0.3s;
}

// 平滑滚动
* {
  scroll-padding-top: 10px;
}

// 滚动条
::-webkit-scrollbar {
  width: 6px;
  background-color: rgba(131, 131, 131, 0.1);
}
::-webkit-scrollbar-thumb {
  background-color: rgba(129, 129, 129, 0.5);
}
::-webkit-scrollbar-thumb:hover {
  border-radius: 2px;
  background-color: rgba(129, 129, 129, 0.9);
}

// 初始化按钮样式
button {
  display: block;
  margin: 0;
  padding: 0;
  border: none;
  background: none;
  box-shadow: none;
  color: var(--text);
}
// 初始化 a 标签样式
a {
  &,
  &:link {
    color: var(--text);
    text-decoration-color: var(--accent);
    text-decoration-style: dashed;
  }
  &:hover {
    text-decoration-style: solid;
  }
  &:visited {
    color: var(--accent);
  }
}

// 初始化标题标签样式
%h1-6 {
  font-weight: bold;
}
h1 {
  @extend %h1-6;
  font-size: xxx-large;
}
h2 {
  @extend %h1-6;
  font-size: xx-large;
}
h3 {
  @extend %h1-6;
  font-size: x-large;
}
h4 {
  @extend %h1-6;
  font-size: large;
}
h5 {
  @extend %h1-6;
  font-size: medium;
}
h6 {
  @extend %h1-6;
  font-size: small;
}
