/* 冷色主题 */
/* 亮色模式 */
@mixin cool-tone-light {
  --text: #071a2c;
  --background: #d8e9f9;
  --primary: #de358d;
  --secondary: #bad8f3;
  --accent: #73c21e;
}
/* 暗色模式 */
@mixin cool-tone-dark {
  --text: #d8e9f9;
  --background: #071a2c;
  --primary: #de358d;
  --secondary: #030b12;
  --accent: #beed8c;
}
