@import "~@/styles/base.scss";

header {
  display: flex;
  padding: 20px 0;
  justify-content: space-between;
  align-items: center;
  user-select: none;
  background-color: var(--background);

  .logo {
    display: flex;
    align-items: center;

    img {
      display: block;
      width: 50px;
      &:hover {
        animation-name: flash;
      }
    }
    h2 {
      margin: 0 10px;
      cursor: pointer;
    }
  }
  nav {
    --animate-delay: 0s;
    --animate-duration: 0.3s;

    &.mini-menu {
      display: block;
    }

    ul {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      font-weight: bold;

      li {
        padding: 0 10px;
      }
    }
  }

  .mini-open {
    display: none;

    button {
      width: 20px;
      height: 10px;
      display: flex;
      flex-flow: column wrap;
      justify-content: space-between;
    }

    span {
      display: block;
      height: 2px;
      width: 100%;
      background-color: var(--text);
      transition: transform 0.3s ease;
    }
    &.active span:first-child {
      transform: translateY(4px) rotate(45deg);
    }
    &.active span:last-child {
      transform: translateY(-4px) rotate(-45deg);
    }
  }
}

@media screen and (max-width: $screen-s) {
  header {
    position: fixed;
    top: 0;
    padding: 0;
    box-sizing: border-box;
    width: 100%;
  }
  nav {
    display: none;
    z-index: 1;
    position: fixed;
    top: 50px;
    width: 100%;
    height: 100vh;
    box-sizing: border-box;
    overflow: hidden;
    background-color: var(--background);
  }
  header > nav > ul {
    flex-flow: column wrap;
    justify-content: stretch;
  }
  div.mini-open {
    display: block;
  }
}
